<template>
  <div class="content-body">
    <div class="container">
      <div class="row">
        <div class="col-xxl-12">
          <div>
            <h2 class="mb-0">Reaperville Staking Statistics</h2>
            <p>
              Earnable reward tokens: 5 million in total for Reaperville
              collection, 1 million in first year, 2.5 million in first 3 years
            </p>
            <p>SVC Total max. supply: 210 million</p>
            <p>Total staked power: {{ totalStakedPower }}</p>
            <p>Base staking power: {{ baseStakingPower }}</p>
            <!-- <p>Released SVC by contract: {{ released }}</p> -->
            <p>Total emission rate: {{ svcEmissionPerHour }} SVC / hour</p>
            <p>
              =
              {{ Math.round((svcEmissionPerHour * 24 * 365) / 1000) / 1000 }}
              million SVC / year
            </p>

            <h1 class="mb-0">---Everything below this line is ADMIN ONLY---</h1>
            <h2 v-if="!isAdmin">(You are not logged into the admin wallet)</h2>
            <h2 v-else>(YOU ARE THE ADMIN)</h2>

            <h2 class="mb-0">Register Reaper Contract as Token Minter</h2>
            <div>
              Reaperville is minter: <strong>{{ reaperIsSvcMinter }}</strong>
            </div>
            <div>
              <button
                class="btn btn-danger"
                type="button"
                :disabled="!reaperIsSvcMinter"
                @click="unregisterReaperAsMinter"
              >
                Unregister as minter
              </button>
              &nbsp;
              <button
                class="btn btn-success"
                type="button"
                :disabled="reaperIsSvcMinter"
                @click="registerReaperAsMinter"
              >
                Register as minter
              </button>
            </div>
            <br />

            <h4>Reaper Contract Balances</h4>
            <p>
              {{
                Math.round(reaperBchBalance / 10 ** 15) / 10 ** 3
              }}
              BCH
            </p>
            <div>
              <button
                class="btn btn-primary"
                type="button"
                :disabled="reaperBchBalance <= 0"
                @click="withdrawBchFromReaper"
              >
                withdraw BCH
              </button>
            </div>

            <br />

            <h2 class="mb-0">Pause Staking Contract</h2>
            Staking is active: {{ !reaperPaused }}
            <p>
              <button
                @click="stakeAdminPause"
                :disabled="reaperPaused"
                class="btn btn-danger"
              >
                Pause
              </button>
              &nbsp;
              <button
                @click="stakeAdminUnpause"
                :disabled="!reaperPaused"
                class="btn btn-success"
              >
                Unpause
              </button>
            </p>
            <!-- <p>
              <button @click="resetBoosts" class="btn btn-warning">Reset boosts</button>
            </p> -->
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import { ethers } from "ethers";
import RewardToken from "../../artifacts/contracts/RewardToken.sol/RewardToken.json";
import Reaperville from "../../artifacts/contracts/Reaperville.sol/Reaperville.json";


export default {
  name: "App",
  components: {},
  data() {
    return {
      rewardTokenContract: null,
      reaperContract: null,

      reaperBchBalance: 0,

      reaperIsSvcMinter: false,

      isAdmin: false,

      reaperPaused: false,
      totalStakedPower: null,
      baseStakingPower: null,
      released: null,
      svcEmissionPerHour: null,
    };
  },
  methods: {
    registerReaperAsMinter: async function () {
      let tx = await this.rewardTokenContract.addMinter(window.ReaperAddress);
      await tx.wait();
      window.location.reload();
    },
    unregisterReaperAsMinter: async function () {
      let tx = await this.rewardTokenContract.removeMinter(
        window.ReaperAddress
      );
      await tx.wait();
      window.location.reload();
    },

    stakeAdminPause: async function () {
      let tx = await this.reaperContract.setPaused(true);
      await tx.wait();
      window.location.reload();
    },
    stakeAdminUnpause: async function () {
      let tx = await this.reaperContract.setPaused(false);
      await tx.wait();
      window.location.reload();
    },

    withdrawBchFromReaper: async function () {
      let tx = await this.reaperContract.withdraw();
      await tx.wait();
      window.location.reload();
    },
  },
  mounted: function () {
    this.provider = new ethers.providers.Web3Provider(window.ethereum, "any");
    this.provider
      .send("eth_requestAccounts", [])
      .then(() => {
        this.signer = this.provider.getSigner();
        let _rewardTokenContract = new ethers.Contract(
          window.RewardTokenAddress,
          RewardToken.abi,
          this.provider.getSigner()
        );
        let _reaperContract = new ethers.Contract(
          window.ReaperAddress,
          Reaperville.abi,
          this.provider.getSigner()
        );
        // let _flexUsdContract = new ethers.Contract(
        //   window.FlexUsdAddress,
        //   RewardToken.abi,
        //   this.provider.getSigner()
        // );

        this.rewardTokenContract = _rewardTokenContract;
        this.reaperContract = _reaperContract;
        // this.flexUsdTokenContract = _flexUsdContract;
      })
      .then(() => {
        this.signer.getAddress().then((address) => {
          if (address === window.DevAddress) {
            this.isAdmin = true;
          }
        });
        this.reaperContract
          .showStakerInfo(window.DevAddress)
          .then((stakerInfo) => {
            this.totalStakedPower = stakerInfo[2];
            this.baseStakingPower = stakerInfo[3];
            this.svcEmissionPerHour =
              Math.round(ethers.utils.formatEther(stakerInfo[4]) * 100000) /
              100000;
          });
        this.provider.getBalance(window.ReaperAddress).then((balance) => {
          this.reaperBchBalance = balance;
        });
        this.reaperContract.isPaused().then((isPaused) => {
          this.reaperPaused = isPaused;
        });

        this.rewardTokenContract
          .isMinter(window.ReaperAddress)
          .then((_isMinter) => {
            this.reaperIsSvcMinter = _isMinter;
          });
      });
  },
};
</script>